import React, {Component} from 'react';

const Context = React.createContext();

const reducer = (state, action) => {
	switch (action.type) {
		case 'SEARCH_TRACKS':
		return {
			...state,
			track_list: action.payload,
			heading: 'Search Results'
		};
		default:
		return state;
	}
};

export class Provider extends Component {

	state = {
		track_list: [],
		heading: 'Top 10 Tracks',
		dispatch: (action) => this.setState(state => reducer(state, action)),
		loading: false,
		toggle_loading: () => this.setState({loading: !this.state.loading})
	}

	componentDidMount () {
		fetch(`http://api.musixmatch.com/ws/1.1/chart.tracks.get?page=1&page_size=10&country=de&f_has_lyrics=1&apikey=${process.env.REACT_APP_MM_KEY}`, {
			headers: {
				"Access-Control-Allow-Origin": "*",
				"Access-Control-Allow-Methods": "GET,HEAD,OPTIONS,POST,PUT",
				"Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept, Authorization"
			}
		})
		.then(response => response.json())
		.then(data => this.setState({track_list: data.message.body.track_list}))
		.catch(error => console.log(error));
	}

  render() {
    return (
      <Context.Provider value={this.state}>
				{this.props.children}
			</Context.Provider>
    );
  }
}

export const Consumer = Context.Consumer;


